import React, { useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import cx from "classnames";
import { formatAmount } from '../../../gmx-test/lib/legacy';
import InputSimple from './../../../../components/UI/input/InputSimple';

const BuilderPositionChart = (props) => {
	const {
		className,
		style
	} = props;

	const [dataObj, setDataObj] = useState({
		strikes: [1800, 1800],
		sentiments: [1, 2],
		premiums: [30, 25],
		amount: 1,
	});
	const [strikesVal, setStrikesVal] = useState("");
	const [sentimentsVal, setSentimentsVal] = useState("");
	const [premiumsVal, setPremiumsVal] = useState("");
	const [isBaseVal, setIsBaseVal] = useState("");
	const [amountVal, setAmountVal] = useState("");

	const isETH = true;
	const minMaxDelta = 0.4;
	const priceStep = 5;

	const calcPnl = (i, closePrice) => {
		const sentiment = dataObj.sentiments[i];
		const premium = dataObj.premiums[i];
		const strikePrice = dataObj.strikes[i];
		const amount = dataObj.amount;
		
		let result;
		switch(sentiment) {
			case 1: // Long Call
				if (closePrice < strikePrice) {
					result = -premium;
				} else {
					result = ((closePrice - strikePrice) * amount) - premium;
				}
				break;
			case 2: // Long Put
				if (closePrice > strikePrice) {
					result = -premium;
				} else {
					result = ((strikePrice - closePrice) * amount) - premium;
				}
				break;
			case -1: // Short Call
				if (closePrice < strikePrice) {
					result = premium;
				} else {
					result = ((strikePrice - closePrice) * amount) + premium;
				}
				break;
			case -2: // Short Put
				if (closePrice > strikePrice) {
					result = premium;
				} else {
					result = ((closePrice - strikePrice) * amount) + premium;
				}
		}
		return Number(result.toFixed(2));
	}

	const createDataset = (dataObj) => {
		const min = Math.round(dataObj.strikes[0] * (1-minMaxDelta) / 100) * 100;
		const max = Math.round(dataObj.strikes[0] * (1+minMaxDelta) / 100) * 100;
		const points = [];
		
		for (let i = 0; i < (max - min) / priceStep; i++) {
			const point = min + i * priceStep;
			points.push(point);
		}
		
		const dataset = [];
		dataObj.sentiments.forEach((sentiment, i) => {
			points.forEach((point, j) => {
				if (i === 0) {
					dataset.push({
						name: formatAmount(point, 0, 0, true),
						pnl: calcPnl(i, point),
					})
				} else {
					dataset[j].pnl = dataset[j].pnl + calcPnl(i, point);
				}
			})
		})

		return dataset;
	}


	return (
		<div className={cx(className, 'BuilderPositionChart')} style={style}>
			<div style={{padding: 15}}>
				<div style={{display: "flex", gap: 10, flexWrap: "wrap"}}>
					<div style={{width: 150}}>
						<InputSimple
							value={amountVal}
							setValue={setAmountVal}
							unit={"Amount"}
						/>
					</div>
					<div style={{width: 275}}>
						<InputSimple
							value={strikesVal}
							setValue={setStrikesVal}
							unit={"Strikes"}
						/>
					</div>
					<div style={{width: 200}}>
						<InputSimple
							value={sentimentsVal}
							setValue={setSentimentsVal}
							unit={"Sentiments"}
						/>
					</div>
					<div style={{width: 275}}>
						<InputSimple
							value={premiumsVal}
							setValue={setPremiumsVal}
							unit={"Premiums"}
						/>
					</div>
					{/* <div style={{width: 200}}>
						<InputSimple
							value={isBaseVal}
							setValue={setIsBaseVal}
							unit={"Is Base"}
						/>
					</div> */}
					<button
						className='btn btn_small'
						onClick={() => {
							if (amountVal && strikesVal && premiumsVal) {
								setDataObj({
									amount: Number(amountVal),
									strikes: strikesVal.split(",").map(Number),
									sentiments: sentimentsVal.split(",").map(Number),
									premiums: premiumsVal.split(",").map(Number),
									isBaseArr: isBaseVal.split(",").map(isBase => {
										if (isBase) {
											return Number.isBase;
										}
									})
								})
							} else {
								alert("Fill out all fields");
							}
						}}
					>
						Apply
						</button>
				</div>
			</div>
			
			<ResponsiveContainer width="100%" height={375}>
				<LineChart
					data={createDataset(dataObj)}
					margin={{
						top: 5,
						right: 30,
						left: 20,
						bottom: 5,
					}}
				>
					<XAxis dataKey="name" interval={19} />
					<YAxis />
					<Tooltip />
					<Legend />
					<Line type="monotone" dot={false} dataKey="pnl" stroke="#8884d8" activeDot={{ r: 4 }} />
				</LineChart>
			</ResponsiveContainer>
		</div>
	);
};

export default BuilderPositionChart;